.navBar {
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 30px;
  border-bottom: 1px solid #232120;
}

.navBar > li {
  font-size: 110%;
  background: #48adf5;
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-preferred-size: 0;
      flex-basis: 0;
  padding: 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  font-weight: 300;
}

.selected {
  font-weight: 600 !important;
  text-decoration: underline;
  background: #207ec1 !important;
}



